:root{
  --header-height:3rem;
  /*==========colors===========*/
  --hue:0;
  --sat:0%;
  --title-color:hsl(var(--hue),var(--sat),20%);
  --title-color-dark:hsl(var(--hue),var(--sat),0%);
  --text-color:hsl(var(--hue),var(--sat),46%);
  --body-color:hsl(var(--hue),var(--sat),98%);
  --container-color:#fff;
  
  /*==========Font and typography==============*/

  /*==========.5rem=8px | 1rem=16px==============*/
  --big-font-size: 3.5rem;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;
  
  /*==========Font weight==============*/


  /*==========Margens Bottom==============*/
  /*==========.5rem=8px | 1rem=16px==============*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2:2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;


  /*==========z index==============*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

 /*==========Responsive typography==============*/


 /*==========BASE==============*/
 *{
  margin: 0 ;
  padding: 0;
  box-sizing: border-box;
 }
 html {
  scroll-behavior: smooth;
 }
 body,
 button,
 input,
 textarea{
  /* font-family: b; */
  font-size: var(--normal-font-size);
 }
 body{
  background-color: var(--body-color);
  color: var(--text-color);
 }
 h1,h2,h3{
  color: var(--title-color);
  font-weight: bold;
 }
 ul{
  list-style: none;
 }
 a{
  text-decoration: none;
 }
 button{
  cursor: pointer;
  border: none;
  outline: none;
 }
 img{
  max-width: 100%;
  height: auto;
 }

 /*==========REUSABLE CSS CLASSES==============*/
 .section{
  padding: 6rem 0 2rem;
 }

 .section_title{
  font-size: var(--h1-font-size);
  color: var(--title-color);
 }
 .section_subtitle{
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: 4rem;

 }

 .section_title,
 .section_subtitle{
  text-align: center;
 }



 

 /*==========BUTTONS==============*/
 .button{
  display: inline-block;
  background-color: var(--title-color-dark);
  color: var(--container-color);
  padding: 1.25rem 2rem;
  border-radius: 1rem;
  font-weight:bold
 }
 .button:hover{
  background-color: var(--title-color-dark);
 }
 .button_icon{
  margin-left: var(--mb-0-5);
 }
 .button--flex{
  display:inline-flex;
  align-items: center;
 }
