.skills_container{
    grid-template-columns: repeat(2, 350px);
    column-gap: 3rem;
    justify-content: center;
}
.skills_content{
    background-color: var( --container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 2rem 4rem;
    border-radius: 1.25rem;
}
.skills_title{
    font-size: var(--normal-font-size);
    font-weight: bold;
    text-align: center;
    margin-bottom: var(--mb-1-5);

}
.skills_box{
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}

.skills_group{
    display: grid;
    align-items: flex-start;
    row-gap: 1rem;

}

.skills_data{
    display: flex;
    column-gap: 0.5rem;
}

.skil_icon{
    font-size: 1rem;
    color: var(--title-color);
}
.skills_name{
    font-size: var(--normal-font-size);
    font-weight: bold;
    line-height: 18px;
}

.skills_level{
    font-size: var(--tiny-font-size);
}